.main {
  width: 20%;
  min-width: 200px;
  margin: 0 auto 0 auto;
  & > div > button {
    margin-bottom: 10px;
    width: 100%;
  }

  &.light {
    > div > button {
      background-color: var(--main-dark-backgroun-color);
      color: var(--main-light-border-color);
      &:hover {
        background-color: var(--main-dark-backgroun-color) !important;
        color: var(--main-light-border-color) !important;
        opacity: 70%;
      }
    }
  }

  &.dark {
    > div > button {
      background-color: var(--main-light-background-color);
      color: var(--main-dark-backgroun-color);
      &:hover {
        background-color: var(--main-light-background-color) !important;
        color: var(--main-dark-backgroun-color) !important;
        opacity: 70%;
      }
    }
  }
}